*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  text-decoration: none;

  color: $main-text;
  transition: color 0.2s;

  @include desktop-only {
    &:hover {
      color: $primary;
    }
  }
}

img {
  max-width: 100%;
  height: auto;
  // display: flex;
}

.reset-list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

body {
  font-family: $font;
  color: $main-text;
  min-width: $mobile;
  min-height: 100vh;
  display: flex;
  flex-direction: column;

  &.js-no-scroll {
    overflow: hidden;
  }
}

main {
  flex-grow: 1;
