.container {
  max-width: $desktop;
  min-width: $mobile;
  margin: 0 auto;
  padding-left: $desktop-gap;
  padding-right: $desktop-gap;

  @include tablet {
    padding-left: $tablet-gap;
    padding-right: $tablet-gap;
  }

  @include mobile {
    padding-left: $mobile-gap;
    padding-right: $mobile-gap;
  }
}

// .container-large {
//   max-width: $desktop-outer;
// }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  margin: -1px;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.only-tablet {
  @media (min-width: $desktop-m) {
    display: none;
  }
}

.only-desktop {
  @include tablet {
    display: none;
  }
}
